.home-works__list-item {
    width: 30%;
    gap: 2rem;
}

.home-works__list-item p {
    font-size: 3.2rem;
    line-height: 3.8rem;
    text-transform: uppercase;
    color: var(--color-black);
    font-weight: var(--fw-300);
}

@media screen and (max-width: 461px) {
    .home-works__list-item {
        width: auto;
        min-width: 15rem;
    }
}

@media screen and (min-width: 461px) and (max-width: 761px) { 
    .home-works__list-item {
        width: auto;
        min-width: 15rem;
        max-width: 35vw;
    }
}
